.administration{
    overflow-x: hidden;
    max-width: 100%;
    height: 100vh;
    .upperContent{
        position: relative;
        margin-bottom: 100px;
        h1{
            color: gold;
            text-align: center;
            font-family: Fantasy;
            @media (max-width: 768px) {
               font-size: 30px;
               margin-bottom: 10px;
              }
        }
        .profPopUp{
          position: absolute;
          right: 0;
          top: 20px;
        }
    }

    .administrationContent{
        justify-content: center;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: auto;
        margin-right: auto;
        gap: 50px;       
    }
}


@keyframes glow {
    0%{
        box-shadow: 0px 0px 20px 5px gold;
    }

    100%{
        box-shadow: 0px 0px 20px 5px rgb(1, 1, 1);
    }
}

.adminBoxes{
            border-top: 1px solid rgba(255, 217, 0, 0.607);
            border-left: 1px solid rgba(255, 217, 0, 0.607);
            border-right: 1px solid rgba(255, 217, 0, 0.607);
            border-bottom: 1px solid rgba(255, 217, 0, 0.607);
            position: relative;
            width: 330px;
            height: 150px;  
            color: gold;
            text-align: center;
            font-size: 30px;
            background: rgba(55, 55, 55, 0.587);
            &:hover{
               animation: glow;
               animation-duration: 2s;
                color: white;
            }
            span{
                position: relative;
                top: 50px;
                height: 100px;
                white-space: nowrap;
                font-family: Fantasy;
            }
}

.textVideoCarouselEffect{
    position: absolute;
}
.textVideoCarouselEffect::before{
    position: absolute;
    box-sizing: border-box;
    background: repeating-linear-gradient(
        -55deg,
        #0c0c0c 12px, 
        rgba(255, 217, 0, 0.607) 20px, 
        #000000 30px,
        rgba(255, 217, 0, 0.607) 20px,
      );
    height: 10px;
    width: 165px;
    content: " ";
    transform: skew(-45deg);
    top: 104px;
    left: -5px;
    border-bottom: 1px solid rgba(255, 217, 0, 0.607);
    border-left: 1px solid rgba(255, 217, 0, 0.607);
    border-right: 2px solid rgba(255, 217, 0, 0.607);
}
.textVideoCarouselEffect::after{
    position: absolute;
    box-sizing: border-box;
    background: repeating-linear-gradient(
        -21deg,
        #0c0c0c 22px, 
        rgba(255, 217, 0, 0.607) 20px, 
        #000000 30px,
        rgba(255, 217, 0, 0.607) 20px,
      );
    height: 10px;
    width: 80px;
    content: " ";
    display: inline-block;
    transform: skew(45deg); 
    border-radius:0px 0px 50% 0px ;
    top: 104px;
    left: 255px;
    border-bottom: 1px solid rgba(255, 217, 0, 0.607);
    border-left: 2px solid rgba(255, 217, 0, 0.607);
    border-right: 1px solid rgba(255, 217, 0, 0.607);
}
.livechat{
    border: 2px solid gold;
    color: gold;
    font-size: 30px;
    margin-left: 30px;
    padding: 10px;
    font-family: fantasy;
    transition: 1s;
    &:hover{
        border: 1px solid rgb(255, 140, 0);  
        color: rgb(255, 140, 0);  
    }
}